import Vue from 'vue'
import Router from 'vue-router'
import Home from '@/views/Home'
import Setup from '@/views/Setup'
import Pl from '@/views/P & L'
import setupRoutes from '@/components/Setup/router/setup.routes'

Vue.use(Router)

export default new Router({
  base: '/vaslogistics/',
  mode: 'history',
  routes: [
    {
      path: '/',
      name: 'Home',
      component: Home
    },
    {
      path: '/setup',
      name: 'Setup',
      component: Setup
    },
    {
      path: '/pl',
      name: 'P & L',
      component: Pl
    },
    ...setupRoutes
  ]
})