<template>
  <v-row justify="center">
    <v-dialog v-model="dialog" persistent max-width="600px">
      <v-card>
        <v-card-title>
          <span class="text-h5">{{ dataa.title }} Item</span>
        </v-card-title>
        <v-card-text>
          <v-container>
            <slot></slot>
          </v-container>
          <!--<small>*indicates required field</small>-->
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="blue darken-1" text @click="close">
            Close
          </v-btn>
          <v-btn color="blue darken-1" text @click="savee">
            Save
          </v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </v-row>
</template>

<script>
export default {
  name: "dia",
  props: ["dataa"],
  data: () => ({
    dialog: true,
  }),

  methods: {
    savee() {
      this.dialog = false;
      this.$emit("save-event", this.dataa.title );
    },
    close() {
      this.dialog = false;
      this.$emit("close-event", 1);
    },
  },
  watch: {
    dataa: function(val) {
      this.dialog = val.dialog;
      console.log(val);
    },
  },
};
</script>
