<template>
  <div>
    <div class="path">
      <v-card class="mx-auto" width="100%" outlined>
        <v-breadcrumbs :items="path" large></v-breadcrumbs>
      </v-card>
    </div>
    <div class="actions">
      <v-card class="mx-auto" width="100%" outlined>
        <v-list-item three-line>
          <v-btn color="#3bd49f" style="color:#ffffff" @click="addDialog">
            <span style="padding-right: 10px; color:#ffffff"
              ><v-icon>mdi-plus</v-icon></span
            >Add</v-btn
          >
        </v-list-item>
      </v-card>
    </div>
    <div class="table">
      <v-data-table
        :headers="headers"
        :items="items"
        class="elevation-1"
        :key="key"
      >
        <template v-slot:item.actions="{ item }">
          <v-icon small class="mr-2" @click="editDialog(item)">
            mdi-pencil-outline
          </v-icon>
          <v-icon small @click="deleteDialog(item)">
            mdi-delete-outline
          </v-icon>
        </template>
      </v-data-table>
    </div>

    <component
      :is="ctr"
      :dataa="d"
      @save-event="saveOrUpdate($event)"
      @close-event="clear"
      ><v-row>
        <v-col cols="12" sm="6" md="6">
          <v-text-field
            label="Value"
            required
            v-model="ds.value"
          ></v-text-field>
        </v-col> </v-row
    ></component>
    <component
      :is="dctr"
      :dataa="deleteddata"
      @deletee-event="del($event)"
    ></component>
  </div>
</template>

<script>
import http from "../services/entities.service.js";
import addd from "../utils/dialogs/dialog";
import deletd from "../utils/dialogs/delete.dialog";

export default {
  name: "sectos",
  components: {
    addd,
    deletd,
  },
  data() {
    return {
      ctr: "",
      dctr: "",
      deleteddata: {},
      d: {},
      ds: {
        type: "",
        value: "",
      },
      path: [
        {
          text: "Home",
          disabled: false,
          href: "/",
        },
        {
          text: "Setup",
          disabled: false,
          href: "/setup",
        },
        {
          text: "Legal Entities",
          disabled: true,
          href: "legalentities",
        },
      ],
      headers: [
        { text: "Legal Entity ID", value: "legalEntityId" },
        { text: "Value", value: "value" },
        { text: "Actions", value: "actions" },
      ],
      items: [{ legalEntityId: 231, value: "vref" }],
      key: 0,
    };
  },

  methods: {
    saveOrUpdate: function(e) {
      e == "Add" ? this.add() : this.put();
    },
    add: async function() {
      const response = await http.add(this.ds);
      console.log(response);
      this.ds = {};
      this.get();
      this.key++;
    },
    get: async function() {
      const response = await http.get();
      this.items = response.data;
    },
    put: async function() {
      const response = await http.put(this.ds.legalEntityId, this.ds);
      console.log(response);
      this.ds = {};
      this.get();
      this.key++;
    },
    del: async function(e) {
      const response = await http.del(e);
      console.log(response);
      this.get();
      this.key++;
    },

    clear() {
      this.ds = {};
    },

    addDialog() {
      this.ctr = "addd";
      this.d = {
        title: "Add",
        dialog: true,
      };
    },

    editDialog(item) {
      (this.ds = {
        legalEntityId: item.legalEntityId,
        type: item.type,
        value: item.value,
      }),
        (this.ctr = "addd");
      this.d = {
        title: "Edit",
        dialog: true,
      };
    },

    deleteDialog(item) {
      this.dctr = "deletd";
      this.deleteddata = {
        dialog: true,
        id: item.legalEntityId,
      };
    },
  },

  mounted() {
    this.get();
  },
};
</script>

<style scoped>
.path {
  margin: 50px 50px 0 50px;
}
.actions {
  margin: 20px 50px 0 50px;
}
.table {
  margin: 20px 50px 0 50px;
}
</style>
