import axios from "axios";

const API_URL = "http://localhost/VAS-PL-Setup/percentages/";

export default {
  async add(data) {
    const options = {
      url: API_URL,
      method: "post",
      data,
    };
    const response = await axios(options);
    return response;
  },
  async get() {
    const response = await axios(API_URL);
    return response;
  },
  async put(id, data) {
    const options = {
      url: API_URL + id,
      method: "put",
      data,
    };
    const response = await axios(options);
    return response;
  },
  async del(id) {
    const options = {
      url: API_URL + id,
      method: "delete",
    };
    const response = await axios(options);
    return response;
  },
};
