<template>
  <v-dialog v-model="dialog" max-width="500px">
    <v-card>
      <v-card-title class="text-h5"
        >Are you sure you want to delete this item?</v-card-title
      >
      <v-card-actions>
        <v-spacer></v-spacer>
        <v-btn color="blue darken-1" text @click="dialog = false">Cancel</v-btn>
        <v-btn color="blue darken-1" text @click="deletee">OK</v-btn>
        <v-spacer></v-spacer>
      </v-card-actions>
    </v-card>
  </v-dialog>
</template>

<script>
export default {
  props: ["dataa"],
  data: () => ({
    dialog: true,
  }),

  methods: {
    deletee() {
      this.dialog = false;
      this.$emit("deletee-event", this.dataa.id);
    },
  },

  watch: {
    dataa: function(val) {
      console.log(324234, val);
      this.dialog = val.dialog;
    },
  },
};
</script>
